import { Priority } from 'shared/interfaces';
import { Suspense, lazy } from 'react';
import { StatusType } from 'pages/Cases/components/Status';
import { DepartmentType } from 'shared/typings/department.types';
import { Loader } from 'shared/components';
import { Route, Switch } from 'react-router';

const FormsConfigTable = lazy(() => import('./FormsConfigTable'));
const ConfigForm = lazy(() => import('./FormsConfigForm'));

export type FeedbackLinkObjType = {
	id: number;
	company: number;
	department: DepartmentType;
	sub_department: { name: string; id: number } | null;
	status: StatusType;
	priority: Priority;
	uuid: string;
};

function Forms({ match }: any) {
	const { path } = match;

	return (
		<div>
			<Suspense fallback={<Loader tiled />}>
				<Switch>
					<Route path={path} exact component={FormsConfigTable} />
					<Route path={`${path}/config/:formType`} exact component={ConfigForm} />
				</Switch>
			</Suspense>
		</div>
	);
}

export default Forms;
